  top: 0;
  transform: rotate(90deg);
}

.burger__stroke:after {
  content: "";
  transition-property: transform, bottom, box-shadow;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
